.employee-card{
  border-radius: 0.25em;
  border: $border;
  padding: 2em;
  &__grid{
    display: grid;
    grid-template-columns: 18.75em calc(100% - 20.75em);
    gap: 2em;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__date{
    color: #5F6065;
  }
  &__items{
    margin-top: 1.375em;
  }
  &__info{
    display: flex;
    flex-direction: column;
    padding: 1.5em;
    border-radius: 0.25rem;
    border: $border;
    .text{
      margin-top: 1.5em;
    }
  }
  &__nav{
    margin-top: auto;
    padding-top: 2.75em;
  }
  &__btn{
    width: 100%;
    img{
      width: 1em;
      margin-left: .625em;
    }
    ._view2{
      display: none;
    }
  }
  &__bottom{
    margin-top: 1.5em;
    padding-top: 1.5em;
    border-top: $border;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn2,
  &__buttons{
    display: flex;
    align-items: center;
  }
  &__btn2{
    font-weight: 500;
    color: #666;
    @include lastM(0 2.75em 0 0);
    svg{
      width: 1.5em;
      margin-right: .5em;
    }
    span{
      font-size: 0.875em;
    }
    &:hover{
      color: $primary;
      ._stroke{
        stroke: $primary;
      }
      ._fill{
        fill: $primary;
      }
    }
  }
}

.employee-info{
  display: flex;
  align-items: flex-start;
  &__icon{
    width: 2em;
    margin-right: .5em;
  }
  .text24{
    line-height: 133%;
  }
  &__items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__item{
    margin: .5em 0 0;
    padding: 0 .5em;
    border-radius: 0.25rem;
    background: #EDEDED;
    height: 2em;
    display: flex;
    align-items: center;
    font-weight: 500;
    span{
      font-size: .75em;
    }
    img{
      width: 1em;
      margin-right: .5em;
    }
  }
}

.statuses{
  margin-top: 1.25em;
}

.status-item{
  display: flex;
  align-items: center;
  padding: 1em 1.25em;
  background: #ECF7F2;
  font-weight: 500;
  &._red{
    background: #FDECEC;
  }
  &._gray{
    background: #F9FAF9;
  }
  &._gray2{
    background: #EDEDED;
  }
  span{
    font-size: .75em;
    line-height: 158%;
  }
  img{
    width: 1em;
    margin-right: .5em;
  }
}

.contacts{
  margin-top: 2em;
}

.contacts-item{
  padding: .75em 1em;
  border-radius: 0.25rem;
  border: $border;
  display: flex;
  align-items: center;
  &__icon{
    width: 3em;
    height: 3em;
    background: #F9FAF9;
    border-radius: 50%;
    @include center;
    margin-right: 1em;
    flex-shrink: 0;
    img{
      width: 1.5em;
    }
  }
  &__info{
    @include grid100gap(.25em);
  }
  &__title{
    font-size: .75em;
    line-height: 133%;
    color: #6E6F75;
  }
  &__desc{
    font-size: 1em;
    line-height: 150%;
  }
  &__btn{
    margin-left: auto;
  }
}

.copy-btn{
  width: 1.25em;
  @include center;
  svg{
    width: 100%;
  }
  &:hover{
    path{
      fill: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .employee-card{
    padding: 1em;
    &__items{
      margin-top: 1.25em;
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__info{
      padding: 1em;
      .text{
        margin-top: 1em;
      }
    }
    &__nav{
      padding-top: 1em;
    }
    &__bottom{
      padding-top: 1em;
    }
  }

  .contacts{
    gap: .75em;
  }
}


@media screen and (max-width: 576px){
  .employee-card{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
    }
    &__buttons{
      margin-top: 1em;
    }
  }

  .contacts-item{
    font-size: .9125em;
  }
}
