.footer{
  color: #fff;
  background-color: $black;
  padding: 2.5em 0;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__title{
    text-transform: uppercase;
    color: #9F9F9F;
  }
  &__link,
  &__title{
    font-size: .875em;
    line-height: 171%;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1em;
  }
  &__link{
    @include lastM(0 0 .57em);
  }
  .socials{
    margin-top: 1.25em;
    &__item{
      @include lastM(0 1.19em 0 0);
    }
  }
  &__bottom{
    margin-top: 2.5em;
    padding-top: 2.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid rgba(#fff,.08);
  }
  &__logo{
    width: 9.4375em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__c{
    font-size: .875em;
    line-height: 171%;
    font-weight: 500;
    color: #888;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2em 0;
    &__top{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2em;
    }
    &__bottom{
      margin-top: 1.5em;
      padding-top: 1.5em;
      flex-direction: column;
      align-items: flex-start;
    }
    &__logo{
      margin: 0 0 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__top{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
    &__c{
      font-size: .75em;
    }
  }
}
