.company-detail{
  &__top{
    border-top: 1px solid #D0D5DD;
    border-bottom: 1px solid #D0D5DD;
  }
  &__bottom{
    padding: 1.5em 0 2em;
    background: #F4F2EE;
  }
}

.tabs{
  display: flex;
  justify-content: center;
}

.tab{
  flex-shrink: 0;
  padding: 0 1.5em;
  height: 4.25em;
  @include center;
  font-weight: 600;
  color: #666;
  @include lastM(0 .875em 0 0);
  position: relative;
  transition: all .3s;
  &:before{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: .25em;
    background-color: $red;
    opacity: 0;
    transition: inherit;
  }
  span{
    font-size: 1em;
  }
  &:hover{
    color: $black;
    cursor: pointer;
  }
  &._active{
    pointer-events: none;
    color: $black;
    &:before{
      opacity: 1;
    }
  }
}

.tab-content2,
.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.glance,
.capital-rating{
  padding: 1.5em 2em;
  border-radius: 0.25em;
  border: 1px solid #D0D5DD;
  background: #FFF;
}

.glance{
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    margin-top: 1.5em;
  }
  .stat-item{
    padding: 1.5em;
    border-radius: 0.25em;
    border: 1px solid #D9D9D9;
    background: #F9FAF9;
    &__icon{
      height: 1.5em;
      margin: .25em 0 .75em .25em;
    }
    &--red{
      border-color: $red;
      background: #FFEDED;
      .text32{
        color: $red;
      }
    }
  }
  &__desc{
    margin-top: 1.25em;
    color: #6E6F75;
    span{
      font-weight: 500;
      color: $black;
    }
    .text14{
      line-height: 171%;
    }
  }
}

.capital-rating{
  &__items{
    margin-top: 1.69em;
    border-radius: 0.25em;
    border: 1px solid #D9D9D9;
    background: #F9FAF9;
    display: grid;
    grid-template-columns: 50% 50%;
  }
}

.capital-item{
  padding: 2em 1em 1.5em;
  &:not(:last-child){
    border-right: 1px solid #D9D9D9;
  }
  &__desc{
    font-weight: 500;
    margin-top: 1em;
    @include center;
    img{
      width: 1em;
      margin-left: .25em;
    }
  }
  &__bottom{
    margin-top: 1.25em;
    padding-top: 1.25em;
    border-top: 1px solid #D9D9D9;
    text-align: center;
  }
  &__nums{
    margin: .75em auto 0;
    display: inline-grid;
    grid-template-columns: 100%;
    gap: 1.5em;
  }
  &__num{
    display: flex;
    align-items: center;
    color: #6E6F75;
    span{
      font-weight: 500;
      color: $black;
    }
    img{
      width: 1em;
      margin-right: .5em;
    }
  }
}

.rating-info{
  display: flex;
  justify-content: center;
  align-items: flex-end;
  img{
    width: 1.3125em;
    margin-right: .125em;
  }
  img,
  &__max{
    transform: translateY(-.25em);
  }
  &__max{
    margin-left: .125em;
    line-height: 100%;
    color: #6E6F75;
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .company-detail{
    &__top{
      .container{
        padding: 0;
      }
    }
    &__bottom{
      .container{
        padding: 0;
      }
    }
  }

  .tabs{
    justify-content: unset;
    overflow-x: auto;
  }

  .tab{
    height: 3em;
    padding: 0 1em;
  }
}


@media screen and (max-width: 576px){
  .glance,
  .capital-rating{
    padding: 1.5em 1em;
  }

  .glance{
    &__items{
      margin-top: 1em;
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__desc{
      margin-top: .75em;
    }
    .stat-item{
      padding: 1em;
    }
  }

  .capital-rating{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .capital-item{
    padding: 1.5em 1em;
    &:not(:last-child){
      border-right: none;
      border-bottom: 1px solid #D9D9D9;
    }
  }
}
