.header{
  padding: 1.5em 0;
  border-bottom: 1px solid #EAECF0;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    width: 10.375em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__btn{
    margin-left: 2.5em;
  }
}


.menu{
  flex-grow: 1;
  margin-left: 3em;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  &:before{
    content: '';
    position: absolute;
    background-color: #EAECF0;
    height: 2em;
    width: 1px;
    right: 100%;
    top: 50%;
    margin-right: 1.5em;
    transform: translate(50%,-50%);
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 1em;
    line-height: 150%;
    letter-spacing: -0.04em;
    font-weight: 500;
    @include lastM(0 2.5em 0 0);
  }
}

.header-search{
  flex-grow: 1;
  max-width: 30.5625em;
  margin-right: auto;
  position: relative;
  input{
    font-size: .875em;
    height: 2.85em;
    width: 100%;
    padding: 0 1em 0 2.85em;
    border-radius: .57em;
    border: 1px solid #EAECF0;
    letter-spacing: -0.035em;
    &::placeholder{
      color: #9B9B9F;
    }
    &:focus{
      border-color: $black;
    }
  }
  &__icon{
    position: absolute;
    left: 1em;
    top: 50%;
    transform: translateY(-50%);
    width: 1em;
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: 1em 0;
    &__inner{
      justify-content: space-between;
    }
    &__logo{
      width: 9em;
      position: relative;
      z-index: 11;
    }
    &__btn{
      margin: 2em 0 0;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .burger{
    z-index: 11;
    width: 2em;
    height: 2em;
    @include center;
    position: relative;
    img{
      transition: all .3s;
      width: 100%;
      @include centerAbsolute;
    }
    ._view2{
      opacity: 0;
    }
  }

  .menu{
    position: fixed;
    display: block;
    width: 100%;
    height: $vh;
    background-color: #fff;
    z-index: 10;
    top: 0;
    left: 0;
    padding: 6em 1em 2em;
    margin: 0;
    max-width: unset;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }

  .header-search{
    margin: 0;
    width: 100%;
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    margin-top: 2em;
    &__link{
      @include lastM(0 0 1em);
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__btn{
      width: 100%;
    }
  }
}
