@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.5em;
    height: 2.75em;
    border-radius: 0.25em;
    font-weight: 600;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 120%;
    }
}

.m-btn-red{
    background-color: $red;
    color: #fff;
    &:hover{
        background-color: darken($red,10%);
        color: #fff;
    }
}

.m-btn-gray-outline{
    border: 1px solid #D0D5DD;
    color: #344054;
    &:hover{
        border-color: $primary;
        background-color: rgba($primary,.3);
        color: #344054;
    }
}
