.company{
  padding: 1.5em 0 2.31em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 40em) 36.375em;
    justify-content: space-between;
    gap: 1.5em;
  }
  &__col{
    display: flex;
    flex-direction: column;
  }
  &__buttons{
    margin-top: auto;
    padding-top: 1em;
    display: flex;
  }
  &__btn{
    height: 3em;
    text-transform: uppercase;
    @include lastM(0 1em 0 0);
    img{
      width: 1em;
      margin-right: .625em;
    }
  }
}

.company-info{
  display: grid;
  grid-template-columns: 11.25em calc(100% - 12.75em);
  gap: 1.5em;
  &__img{
    padding-bottom: 100%;
    position: relative;
    border: 2px solid #F3F3F3;
    border-radius: 0.25em;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc{
    margin-top: .25em;
    color: #646464;
  }
  &__links{
    display: flex;
    margin-top: 1em;
  }
  &__link{
    @include center;
    @include lastM(0 .5em 0 0);
    font-weight: 500;
    border-radius: 0.25em;
    height: 2em;
    background: #EDEDED;
    padding: 0 .5em;
    &:hover{
      background: darken(#EDEDED,10%);
      color: $black;
    }
    span{
      font-size: .75em;
      line-height: 120%;
    }
    img{
      width: 1em;
      margin-right: .5em;
    }
  }
  .socials{
    margin-top: 2em;
  }
}

.socials{
  display: flex;
  &__item{
    width: 1.5em;
    @include lastM(0 1.5em 0 0);
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      ._fill{
        fill: $red;
      }
    }
  }
}

.company-stat{
  border: 1px solid #D0D5DD;
  border-radius: 0.25em;
  &__top{
    display: grid;
    grid-template-columns: calc(100% - 12.375em) 12.375em;
  }
  &__items{
    padding: 1.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }
  &__bottom{
    height: 3.5em;
    border-radius: 0 0 .25em .25em;
    background-color: $primary;
    @include center;
    color: #fff;
    .text24{
      margin-left: .41em;
    }
  }
}

.stat-item{
  &__icon{
    height: 1.25em;
    display: block;
    margin-bottom: .625em;
  }
  &__desc{
    margin-top: .25em;
    color: #475467;
  }
}

.stat-info{
  padding: 2.5em 1.125em;
  border-left: 1px solid #D0D5DD;
  text-align: center;
  &__top{
    position: relative;
    margin-bottom: 1em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__num{
    position: absolute;
    bottom: .25em;
    left: 50%;
    transform: translateX(-50%);
  }
  &__title{
    font-weight: 500;
    color: #727272;
    span{
      font-weight: 700;
      color: $primary;
    }
  }
  &__time{
    margin-top: .75em;
    @include center;
    color: #6E6F75;
    img{
      width: 1em;
      margin-right: .31em;
    }
  }
}


@media screen and (max-width: 992px){
  .company{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .company{
    &__buttons{
      display: block;
      padding-top: 1.5em;
    }
    &__btn{
      width: 100%;
      @include lastM(0 0 .5em);
    }
  }

  .company-info{
    grid-template-columns: 100%;
    &__img{
      width: 11.5em;
      height: 11.5em;
      padding: 0;
      margin: 0 auto;
    }
  }

  .company-stat{
    &__top{
      grid-template-columns: 100%;
    }
  }

  .stat-info{
    order: -1;
    border-left: none;
    border-bottom: 1px solid #D0D5DD;
    padding: 2em 3em;
  }
}
