.complaints{
  padding: 2em;
  background-color: #fff;
  border-radius: 0.25em;
  border: $border;
  &__items{
    margin-top: 2em;
    @include grid100gap(1.5em);
  }
}

.m-badge{
  text-transform: uppercase;
  text-align: center;
  padding: 1.125em 1em;
  border-radius: 0.25em;
  background-color: $primary;
  color: #fff;
  .text16{
    margin-left: .25em;
  }
}

.complaint-card{
  border-radius: 0.25em;
  border: $border;
  padding: 1.625em 2em 2em;
  &._active{
    .summary__btn{
      img{
        transform: rotate(-180deg);
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    .complaint-card__bottom{
      display: block;
    }
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__nav{
    display: flex;
  }
  &__btn{
    width: 2.5em;
    height: 2.5em;
    padding: 0;
    @include lastM(0 1em 0 0);
    img{
      width: 1em;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: 20.625em calc(100% - 20.625em - 1.5em);
    gap: 1.5em;
    margin-top: 1.375em;
  }
  &__sticky{
    position: sticky;
    top: .5em;
  }
  &__wrap{
    display: grid;
    grid-template-columns: calc(100% - 15.75em) 14.25em;
    gap: 1.5em;
  }
  &__bottom{
    margin-top: 1.5em;
    display: none;
  }
}

.props{
  @include grid100gap(1em);
}

.prop-item{
  display: grid;
  grid-template-columns: 9em calc(100% - 9.25em);
  gap: .25em;
  align-items: center;
  &__title{
    color: #5F6065;
  }
  &__desc{
    font-weight: 500;
  }
  &__title,
  &__desc{
    font-size: .875em;
    line-height: 128%;
  }
  &__badge{
    display: inline-flex;
    align-items: center;
    margin-right: auto;
    padding: 0 .5em;
    height: 1.375em;
    border-radius: 0.875em;
    background: #DEE7F7;
    font-weight: 500;
    color: #306BCD;
    img{
      width: .75em;
      margin-right: .25em;
    }
    span{
      font-size: .875em;
    }
  }
}

.attachment{
  padding: 1.5em;
  margin-top: 1.5em;
  border-radius: 0.25em;
  border: $border;
  background: #F9FAF9;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    font-weight: 500;
    color: #306BCD;
    &:hover{
      color: $red;
    }
  }
  &__items{
    margin-top: 1em;
    @include grid100gap(1em);
  }
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 500;
    padding: .75em;
    border-radius: 0.5em;
    background: #FFF;
    img{
      width: 1.5em;
      margin-left: 1em;
    }
  }
}

.summary{
  border-radius: 0.25em;
  border: $border;
  padding: 1.5em;
  display: flex;
  flex-direction: column;
  .text{
    margin-top: 1.5em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.5em;
  }
  &__btn{
    width: 100%;
    ._view2{
      display: none;
    }
    img{
      width: 1em;
      margin-left: .625em;
      transition: all .3s;
    }
  }
}

.text{
  color: #6E6F75;
  p{
    font-size: .875em;
    line-height: 171%;
    @include lastM(0 0 1.125em);
    span{
      font-weight: 500;
      color: $black;
    }
  }
}

.complaint-stat{
  border-radius: 0.25em;
  border: $border;
  display: flex;
  flex-direction: column;
  &__top{
    padding: 1.5em 1em;
  }
  &__items{
    border-top: $border;
    padding: 1.5em;
    @include grid100gap(1.5em);
  }
  &__bottom{
    margin-top: auto;
    padding: .78em;
    color: #fff;
    background-color: $primary;
    border-radius: 0 0 .25em .25em;
    @include grid100gap(.5em);
    text-align: center;
  }
}

.stat-item2{
  display: flex;
  align-items: center;
  font-weight: 500;
  color: #5F6065;
  img{
    width: 1.5em;
    margin-right: .5em;
  }
  &__title,
  &__num{
    font-size: .875em;
  }
  &__num{
    font-weight: 600;
    color: $black;
    margin-left: auto;
    padding-left: .25em;
  }
}

.complaint-detail{
  border-radius: 0.25em;
  border: $border;
  margin-top: 1.5em;
  display: none;
  .tabs{
    justify-content: unset;
    padding: 0 2em;
    position: relative;
    z-index: 2;
  }
  .tab{
    height: 3.5em;
    &:before{
      background-color: $primary;
    }
  }
  &__content{
    margin-top: -.0625em;
    padding: 2em;
    background: #F9FAF9;
    border-top: $border;
  }
  .text{
    margin-top: 1.5em;
  }
  &__quote{
    margin-top: 1.5em;
    padding: 1em 1.5em;
    border-radius: 0.25em;
    background: #FFEDED;
    .text{
      margin: 0;
      p{
        display: inline;
      }
    }
  }
}

.gallery{
  margin-top: .75em;
  display: flex;
  flex-wrap: wrap;
}

.gallery-item{
  width: 10.625em;
  height: 7.5em;
  margin: .75em .75em 0 0;
  img{
    border-radius: 0.25em;
    border: $border;
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
  }
}

.experience{
  margin-top: 1.5em;
  @include grid100gap(1.5em);
  &__item{
    @include grid100gap(.5em);
  }
  &__title,
  &__desc{
    span{
      font-size: .875em;
      line-height: 171%;
    }
  }
  &__desc{
    font-weight: 500;
    border-radius: 0.25em;
    background: #EFF4FB;
    padding: .75em 1em;
    color: #306BCD;
  }
}

.co-rating{
  border-radius: .25em;
  overflow: hidden;
  display: grid;
  grid-template-columns: 11.8125em calc(100% - 11.8125em);
  margin-top: 1.5em;
  &__col1,
  &__col2{
    padding: 1.5em;
  }
  &__col1{
    background-color: $primary;
    @include center;
  }
  &__col2{
    background-color: #fff;
  }
  .rating-info{
    color: #fff;
    &__max{
      color: #fff;
    }
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.125em));
    gap: 1em 2.25em;
  }
}

.prop{
  display: flex;
  align-items: center;
  &__icon{
    width: 1.5em;
    margin-right: .625em;
  }
  &__info{
    @include grid100gap(.25em);
  }
  &__title,
  &__num{
    font-size: 1em;
    line-height: 150%;
  }
  &__title{
    font-weight: 500;
  }
  &__num{
    font-weight: 700;
    margin-left: auto;
    padding-left: .25em;
    align-self: flex-start;
  }
  &__desc{
    font-size: .75em;
    line-height: 133%;
    color: #6E6F75;
  }
}

.pagination{
  margin-top: 2em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__items{
    display: flex;
  }
  &__item{
    padding: 0 1.5em;
    height: 2.5em;
    border-radius: 0.25em;
    border: 1px solid #D0D5DD;
    background: #F9FAF9;
    @include center;
    font-size: 1em;
    font-weight: 600;
    color: #344054;
    @include lastM(0 .5em 0 0);
    &._active,
    &:hover{
      background-color: $black;
      color: #fff;
    }
  }
  &__more{
    display: flex;
    align-items: center;
    svg{
      width: 1em;
      margin-left: .5em;
    }
    span{
      font-size: .875em;
    }
    ._view2{
      display: none;
    }
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .complaint-card{
    padding: 1em;
    &__top{
      display: block;
    }
    &__nav{
      margin-top: 1em;
    }
    &__grid{
      grid-template-columns: 100%;
    }
    &__sticky{
      position: static;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
      align-items: flex-start;
    }
  }

  .prop-item{
    grid-template-columns: 8em calc(100% - 8.25em);
  }

  .attachment{
    margin: 0;
    padding: .75em;
    &__items{
      gap: .5em;
    }
  }

  .complaint-detail{
    width: calc(100% + 2em);
    margin-left: -1em;
    border-right: none;
    border-left: none;
    border-radius: 0;
    .tabs{
      padding: 0 1em;
    }
    &__content{
      padding: 1em;
    }
    .text,
    &__quote{
      margin-top: 1em;
    }
  }

  .gallery{
    margin-top: .25em;
  }

  .experience{
    margin-top: 1em;
    gap: .75em;
  }

  .co-rating{
    margin-top: 1em;
  }
}


@media screen and (max-width: 576px){
  .complaints{
    padding: 1em;
  }

  .m-badge{
    font-size: .875em;
  }

  .complaint-card{
    &__title{
      font-size: 1.125em;
    }
    &__sticky,
    &__wrap{
      grid-template-columns: 100%;
    }
  }

  .summary{
    padding: 1em;
  }

  .complaint-stat{
    &__top,
    &__items{
      padding: 1em;
    }
    &__items{
      gap: 1em;
    }
  }

  .complaint-detail{
    &__quote{
      padding: .5em 1em;
    }
  }

  .gallery-item{
    width: 100%;
    height: auto;
    margin-right: 0;
  }

  .experience{
    &__desc{
      padding: .5em .75em;
    }
  }

  .co-rating{
    grid-template-columns: 100%;
    &__col1,
    &__col2{
      padding: 1em;
    }
    &__items{
      grid-template-columns: 100%;
      gap: .75em;
    }
  }

  .pagination{
    margin-top: 1.5em;
    flex-direction: column;
    align-items: flex-start;
    &__items{
      margin-top: 1em;
    }
    &__more{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}
