.breadcrumbs {
    position: relative;
    z-index: 2;
    margin-bottom: 1.5em;
    &__item {
        position: relative;
        display: inline;
        color: #999;
        font-weight: 500;
        font-size: .875em;
        line-height: 100%;
        letter-spacing: -0.04em;
        &:not(:first-child) {
            margin-left: 1.75em;
            &:before {
                content: '';
                position: absolute;
                background-image: url(../img/arrow.svg);
                @include bgImgSize(100%);
                z-index: 1;
                width: .857em;
                height: .857em;
                top: .25em;
                right: 100%;
                margin-right: .5em;
            }
        }
        &:last-child{
            color: $black;
            pointer-events: none;
        }
    }
}
